import React, { useState, useEffect } from 'react';
import * as localService from '../../services/localStorage';
import { Link } from 'react-router-dom';
import FastAverageColor from 'fast-average-color';

const MainTop = ({promise,resource = null,title = false,history}) =>{
    resource = promise ? promise.read() : resource;

    if(resource.length !== 0){return(
        <div className="main-top">
            <div className="main-top__grid">
                {resource.map((item)=><CardDominant key={item.id} item={item}/>)}
            </div>
        </div>
    )}else return false;
};

const CardDominant = ({item}) =>{
    const[dominantColor,setDominantColor] = useState('');
    useEffect(()=>{
        const fac = new FastAverageColor();
        if(item.backdrop !== null){
            const img = new Image();
            img.crossOrigin = "anonymous";
            img.src = item.backdrop;
            fac.getColorAsync(img, {
                ignoredColor: [0, 0, 0, 255] // black
            }).then(function(color) {
                setDominantColor(`0 0 30rem 3rem ${color.rgba}`);
            });
        };
        return function cleanup(){
            fac.destroy();
        };
    },[item]);
    const hoverStyle = {boxShadow: dominantColor,borderRadius: '4%'};

    return (
        <div className="main-top__card" style={hoverStyle}>
            <Link className="main-top__link"
                to={`/${item.type}/${item.id}`}
                onClick={()=>localService.addLatest(item)}/>
            <div className="main-top__shadow"/>
            <img className="main-top__backdrop" alt="" src={item.backdrop}/>
            <h1 className="main-top__name">{item.name}</h1>
        </div>
    )
}

